.img-grid{
    margin: 20px auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px   ;
}

.img-wrap{
    overflow: hidden;
    height: 0;
    padding: 50% 0;
    position: relative;
    opacity: 0.8;
}

.img-wrap img{
    min-width: 100%;
    min-height: 100%;
    max-width: 150%;
    position: absolute;
    top: 0;
    left: 0;
}

@media screen and (max-width:1224px){
    .img-grid{
        grid-template-columns: repeat(2,1fr);
    }

}


@media screen and (max-width:700px){
    .img-grid{
        grid-template-columns: repeat(1,1fr);
    }

}
    
